import buildService from '@/pages/index/helper/service'

// StringObject,Stringurl(method),Objectaxios(config),dataparams
const settings = {
  userrejectGet: {
    url: '/retail/userreject/',
    method: 'get'
  },
  userrejectPost: {
    url: '/retail/userreject/',
    method: 'post'
  },
  userrejectDelete: {
    url: '/retail/userreject/',
    method: 'delete'
  }
}

export default buildService(settings)



